import FormSelect from './form-select';
import { Fragment } from 'react';

const wp = window.wp;
const { __ } = wp.i18n;

const { SelectControl } = wp.components;

class ReportsFilter extends wp.element.Component {
	render() {
		const {
			name,
			range,
			onRangeChange,
			selectedFormJson,
			onFormsChange,
			category,
			onCategoryChange,
			assignee,
			assignees,
			onAssigneeChange,
			step_id, /* eslint-disable-line */
			steps,
			onStepChange,
		} = this.props;

		const selectedForms = ! selectedFormJson
			? []
			: JSON.parse( selectedFormJson );

		return [
			<Fragment key={ name }>
				<SelectControl
					label={ __( 'Range', 'gravityflowblocks' ) }
					value={ range }
					onChange={ ( rng ) => {
						onRangeChange( rng );
					} }
					options={ [
						{
							value: 'last-12-months',
							label: __( 'Last 12 months', 'gravityflowblocks' ),
						},
						{
							value: 'last-6-months',
							label: __( 'Last 6 months', 'gravityflowblocks' ),
						},
						{
							value: 'last-3-months',
							label: __( 'Last 3 months', 'gravityflowblocks' ),
						},
					] }
				/>
				<FormSelect
					isMulti={ false }
					selectedForms={ selectedForms }
					onFormsChange={ ( sf ) => {
						onFormsChange( sf );
					} }
				/>
				{ selectedForms.value !== '' && (
					<SelectControl
						label={ __( 'Category', 'gravityflowblocks' ) }
						value={ category }
						onChange={ ( ct ) => {
							onCategoryChange( ct );
						} }
						options={ [
							{
								value: 'month',
								label: __( 'Month', 'gravityflowblocks' ),
							},
							{
								value: 'assignee',
								label: __( 'Assignee', 'gravityflowblocks' ),
							},
							{
								value: 'step',
								label: __( 'Step', 'gravityflowblocks' ),
							},
						] }
					/>
				) }
				{ category === 'step' && (
					<SelectControl
						label={ __( 'Step', 'gravityflowblocks' ) }
						value={ step_id } /* eslint-disable-line */
						onChange={ ( sid ) => {
							onStepChange( sid );
						} }
						options={ steps }
					/>
				) }
				{ assignees && (
					<SelectControl
						label={ __( 'Assignee', 'gravityflowblocks' ) }
						value={ assignee }
						onChange={ ( ag ) => {
							onAssigneeChange( ag );
						} }
						options={ assignees }
					/>
				) }
			</Fragment>,
		];
	}
}

export default ReportsFilter;
