const wp = window.wp;
import Filters from './filters';
const { __ } = wp.i18n;

const { Spinner } = wp.components;

class EntryTable extends wp.element.Component {
	render() {
		const {
			stepHighlight,
			idColumn,
			submitterColumn,
			stepColumn,
			actionsColumn,
			lastUpdated,
			dueDate,
			className,
			selectedForms,
			selectedFields,
			entryData,
			liveData,
			columnOrder,
			displayFilters,
		} = this.props;

		// temp to allow separate styling for each box, and will work with existing saved instances.
		const isInbox = displayFilters === undefined;

		if ( entryData === undefined || entryData.rows === undefined ) {
			return (
				<p className={ className }>
					<Spinner />
					{ __( 'Loading', 'gravityflow' ) }
				</p>
			);
		}
		const columnMappings = {
			id: { enabled: idColumn, title: __( 'ID', 'gravityflow' ) },
			actions: { enabled: actionsColumn, title: '' },
			form_title: {
				enabled: selectedForms.length === 0 || selectedForms.length > 1,
				title: __( 'Form', 'gravityflow' ),
			},
			created_by: {
				enabled: submitterColumn,
				title: __( 'Submitter', 'gravityflow' ),
			},
			workflow_step: {
				enabled: stepColumn,
				title: __( 'Step', 'gravityflow' ),
			},
			workflow_final_status: {
				enabled: true,
				title: __( 'Status', 'gravityflow' ),
			},
			date_created: {
				enabled: true,
				title: __( 'Submitted', 'gravityflow' ),
			},
			fields: selectedFields,
			last_updated: {
				enabled: lastUpdated,
				title: __( 'Last Updated', 'gravityflow' ),
			},
			due_date: {
				enabled: dueDate,
				title: __( 'Due date', 'gravityflow' ),
			},
		};

		const orderedColumns = [];
		columnOrder.forEach( function( key ) {
			orderedColumns[ key ] = columnMappings[ key ];
		} );
		const columns = [];
		Object.keys( orderedColumns ).forEach( function( key ) {
			if ( key === 'fields' ) {
				selectedFields.forEach( function( field ) {
					columns.push( { key: field.value, title: field.label } );
				} );
			} else if ( orderedColumns[ key ].enabled ) {
				columns.push( {
					key,
					title: orderedColumns[ key ].title,
				} );
			}
		} );

		if ( ! liveData ) {
			const row = {};
			columns.forEach( function( column ) {
				row[ column.key ] = (
					<div
						style={ {
							width: 'auto',
							backgroundColor: isInbox ? '#f4f5fb' : '#eee',
							height: '10px',
						} }
					>
						&nbsp;
					</div>
				);
			} );
			entryData.rows = [ ...Array( 10 ) ].map( ( _, i ) => {
				const newRow = {};
				Object.assign( newRow, row );
				newRow.id = i + 1;
				if ( i === 0 ) {
					newRow.step_highlight = 'red';
				}
				return newRow;
			} );
		}

		const wrapperClass = isInbox
			? 'gravityflow-entry-table--inbox'
			: 'gravityflow-entry-table--status';

		return (
			<div className={ wrapperClass }>
				{ displayFilters && (
					<Filters formsCount={ selectedForms.length } />
				) }
				<table className={ 'gravityflow-entry-table' }>
					<thead>
						{ isInbox && (
							<tr colSpan={ columns.length }>
								<th
									colSpan={ columns.length }
									className="gravityflow-entry-table__header"
								>
									<div className="gravityflow-entry-table__header-div">
										<i className="gravityflow-entry-table__header-search-icon gflow-icon gflow-icon--search" />
										<div className="gform-input gravityflow-entry-table__header-search-input" />
										<i className="gravityflow-entry-table__header-gear-icon gflow-icon gflow-icon--settings" />
									</div>
								</th>
							</tr>
						) }
						<tr className="gravityflow-entry-table__header-columns">
							{ columns.map( ( column ) => {
								const key = column.key;
								return <th key={ key }>{ column.title }</th>;
							} ) }
						</tr>
					</thead>

					<tbody>
						{ entryData.rows.map( ( row ) => {
							const highlightColor = stepHighlight
								? row.step_highlight
								: '';
							return (
								<tr
									key={ row.id }
									style={ { borderColor: highlightColor } }
								>
									{ columns.map( ( column ) => {
										const key = column.key;
										const val =
											key === 'actions' ? (
												<div
													className={
														'gflow-grid__actions'
													}
												>
													<i
														className="gflow-grid__actions-trigger gflow-icon gflow-icon--more-vertical"
														style={ {
															alignItems:
																'center',
															display: 'flex',
															height: '100%',
															justifyContent:
																'center',
															transform:
																'rotate(90deg)',
														} }
													/>
												</div>
											) : (
												row[ key ]
											);
										return <td key={ key }>{ val }</td>;
									} ) }
								</tr>
							);
						} ) }
					</tbody>
				</table>
			</div>
		);
	}
}

export default EntryTable;
